/* Styles go here */
* {
  box-sizing: border-box;
}

::-webkit-scrollbar {
    width: 12px;
	background:#fff !important;
}
::-webkit-scrollbar-thumb {
    background: #3cb371;
	border:0;
}
::-webkit-scroll-thumb:hover {background:#3cb371}
::-webkit-scrollbar-track {
	background:#fff;
}

#page-wrap {
  padding-bottom: 10px;
  padding-top: 10px;
}
.color-nav {background-color: #3cb371; }
#account {color:#fff; font-size:11px;}
